import React from 'react';
import { RouteComponentProps } from 'react-router';
import { Link } from "react-router-dom";

const SPREADSHEET_URL: string =
  'https://spreadsheets.google.com/feeds/list/1jDoKicjNhz0ZOJRGP0K3Dw-4ioqcc1xsLyOYz0cM_rU/od6/public/values?alt=json';

const STYLE: React.CSSProperties = {
  width: '600px',
};

const PAGE_QUANTITY: number = 5;

// propsとstateで利用するプロパティ名
enum PropName {
  page = 'page',
  dataList = 'dataList',// 取得したデータ全体

  embedcode = 'embedcode',// データベースの列名(googleの仕様上アンダーバーが消える)
}

interface RouterPropType extends RouteComponentProps<{
  [PropName.page]: string
}> {
}

interface PropType extends RouterPropType {
}

interface DataType {
  [PropName.embedcode]: string
}

interface StateType {
  [PropName.page]: string;
  [PropName.dataList]: DataType[];
}

export default class App extends React.Component<PropType, StateType> {
  constructor(props: PropType) {
    super(props);

    this.state = {
      [PropName.page]: '',
      [PropName.dataList]: [{ [PropName.embedcode]: '' }],
    };
  }

  getActiveDataList(): string {
    const page: string = this.props.match.params[PropName.page];
    const startIndex: number = (parseInt(page) - 1) * PAGE_QUANTITY;
    const activeList: DataType[] =
      this.state.dataList.slice(startIndex, startIndex + PAGE_QUANTITY);
    return activeList
      .map(data => {
        return `<div class="card m-2"><div class="card-body">${data[PropName.embedcode]}</div></div>`;
      })
      .join(`\n`);
  }

  getNextPage(): string {
    const page: string = this.props.match.params[PropName.page];
    let pageNum: number = parseInt(page);
    pageNum++;
    return pageNum.toString();
  }

  getPreviousPage(): string {
    const page: string = this.props.match.params[PropName.page];
    let pageNum: number = parseInt(page);
    pageNum--;
    pageNum = Math.max(pageNum, 1);//ページの最小値
    return pageNum.toString();
  }

  componentDidUpdate(): void {
    window.scroll(0, 0);
  }

  render(): JSX.Element {
    return (
      <div className="mx-auto d-flex flex-column align-items-center" style={STYLE}>
        <div>
          <img src="./top.png" alt="top_image" />
        </div>

        <div dangerouslySetInnerHTML={{
          __html: this.getActiveDataList()
        }}>
        </div>

        <div className="m-2 d-flex justify-content-between">
          <div>
            <Link to={this.getPreviousPage()} >
              <img src="./navigationj_prev.png" alt="navigationj_prev.png" />
            </Link>
          </div>
          <div>
            <Link to={this.getNextPage()}>
              <img src="./navigationj_next.png" alt="navigationj_next.png" />
            </Link>
          </div>
        </div>
      </div>
    );
  }

  componentDidMount(): void {
    fetch(SPREADSHEET_URL)
      .then(res => res.json())
      .then(json => {
        const dataList: DataType[] = [];
        for (let item of json.feed.entry) {
          let s: string = item.gsx$embedcode.$t;
          s = s.replace('http://youtube.com', 'https://youtube.com');
          let data: DataType = {
            [PropName.embedcode]: s
          };
          dataList.push(data);
        }
        this.setState({
          [PropName.dataList]: dataList.reverse(),// 新しいもの順に表示
        });
      });
  }
}